import React, { Component } from "react";
import moment from "moment";
import Button from "../../../../components/uielements/button";
import EditableTable from "./editableTable";
import IntlMessages from "../../../../components/utility/intlMessages";
import ModalSearchSupplier from "./modalSearchSupplier";
import NumberFormat from "../../../App/NumberFormat";
import notification from "../../../../components/notification";
import {
  Checkbox,
  Col,
  DatePicker,
  Divider,
  Empty,
  Form,
  Icon,
  Input,
  Row,
  Select,
  Spin,
  Tooltip
} from "antd";
import ModalAddSupplierType from "./modalAddSupplierType";
import getLanguage from "../../../App/MyLanguange";
import AddModule from "../../../App/AddModule";

const InputGroup = Input.Group;
const Option = Select.Option;
const { RangePicker } = DatePicker;
const { TextArea } = Input;

class SupplierPriceListCreateForm extends Component {
  state = {
    check_code: false,
    dataSource: [],
    expired_date: null,
    id_supplier: "",
    id_location: "",
    item: null,
    items: [],
    priceListType: parseInt(1),
    search: null,
    searchSupp: null,
    selected_items: [],
    start_date: null,
    supplier_name: "",
    viewedPL: false,
    visibleModalSearchSupplier: false,
    visibleModalUnprocessedPriceList: false
  };

  componentDidMount() {
    const { user } = this.props.auth;
    let { default_employee_location } = user ? user : [];
    default_employee_location = default_employee_location
      ? default_employee_location
      : [];

    if (default_employee_location && default_employee_location.id_location) {
      const id_location = default_employee_location.id_location;
      this.handleSelectLocation(id_location);
    }
  }

  componentDidUpdate() {}

  handleChangeCode = () => {
    const { setFieldsValue } = this.props.form;
    this.setState({ check_code: !this.state.check_code });
    if (this.state.check_code === false) {
      setFieldsValue({
        ["pricelist_code"]: null
      });
    }
  };

  handleChangeRangePicker = (value, dateString) => {
    this.setState({ start_date: dateString[0], expired_date: dateString[1] });
  };

  handleChangeSelectPriceListType = value => {
    const { id_location } = this.state;
    const priceListType = value;
    const { getSuppliersBasedOnLocation } = this.props;
    if (this.state.priceListType !== value) {
      if (value === 1) {
        getSuppliersBasedOnLocation(id_location);
      }
      this.setState({ priceListType });
      this.props.form.setFieldsValue({
        id_supplier: null,
        id_currency: null
      });
    }
  };

  handleChangeSelectSupplierType = value => {
    this.props.form.setFieldsValue({
      id_supplier_type: value
    });
  };

  handleDeleteItem = key => {
    var dataSource = [...this.state.dataSource];
    // console.log(key, dataSource);
    dataSource = dataSource.filter(item => item.index_no !== key);
    var i = 0;
    let selected_items = [];
    dataSource.map(item => {
      selected_items.push(item.id_item + "_" + item.id_unit);
      i = i + 1;
      item.index_no = i;
    });
    this.setState({
      dataSource,
      selected_items
    });
  };

  handleModalSearchSupplier = () => {
    const { visibleModalSearchSupplier } = this.state;
    visibleModalSearchSupplier
      ? this.setState({ visibleModalSearchSupplier: false })
      : this.setState({ visibleModalSearchSupplier: true });
  };

  handleLoading = () => {
    if (this.props.supplierPriceList.loading) return true;
    else return false;
  };

  handleSearchSupplier = value => {
    const { id_location } = this.state;
    const { getSuppliersBasedOnLocation } = this.props;
    this.setState({ searchSupp: value });
    // this.props.form.setFieldsValue({ id_supplier: value });
    getSuppliersBasedOnLocation(id_location, value);
  };

  handleSearchItem = value => {
    const { getItemsForSupplierPriceList } = this.props;
    const { id_location } = this.state;
    if (id_location) {
      this.setState({ search: value });
      getItemsForSupplierPriceList(id_location, value);
    }
  };

  handleSelectSupplier = value => {
    const { id_location, searchSupp } = this.state;
    const { currencies, suppliers } = this.props.supplierPriceList;
    let setStateData = {};

    if (searchSupp) {
      const { getSuppliersBasedOnLocation } = this.props;
      getSuppliersBasedOnLocation(id_location);
      setStateData.searchSupp = null;
    }
    setStateData.id_supplier = value;

    const findSupp = suppliers.find(
      curr_data => curr_data.id_supplier === value
    );
    if (findSupp) {
      const curr = currencies.find(
        data => data.id_currency === findSupp.id_currency
      );
      const id_currency = curr.id_currency;
      this.props.form.setFieldsValue({
        id_currency
      });
    }

    this.setState(setStateData);
  };

  handleSelectItem = value => {
    if (value) {
      const { items, id_location } = this.props.supplierPriceList;
      const { search, selected_items } = this.state;
      let idItem = value.slice(0, value.indexOf("_"));
      let idUnit = value.slice(value.indexOf("_") + 1, value.length);
      this.setState({
        item: items.filter(
          item => item.id_item === idItem && item.id_unit === idUnit
        )[0]
      });

      if (search) {
        const { getItemsForSupplierPriceList } = this.props;
        getItemsForSupplierPriceList(id_location);
        this.setState({ search: null });
      }
      // push ke dataSource
      const { dataSource } = this.state;
      const [searchItem] = dataSource.filter(
        item => item.id_item === idItem && item.id_unit === idUnit
      );
      if (searchItem) {
        // searched item is already in the dataSource
      } else {
        const [newItem] = items.filter(
          item => item.id_item === idItem && item.id_unit === idUnit
        );
        const count = dataSource.length + 1;
        this.setState({
          selected_items: [...selected_items, value],
          dataSource: [
            ...this.state.dataSource,
            {
              ...newItem,
              key: newItem.id_item + "_" + newItem.id_unit,
              index_no: count,
              item_price: 0
            }
          ]
        });
      }
    }
  };

  handleSelectLocation = id_location => {
    // const { search } = this.state;
    this.setState({
      id_location,
      dataSource: [],
      search: null,
      selected_items: []
    });
    const { setFieldsValue } = this.props.form;
    // this.setState({  });
    const {
      getItemsForSupplierPriceList,
      getSuppliersBasedOnLocation
    } = this.props;
    getItemsForSupplierPriceList(id_location);
    getSuppliersBasedOnLocation(id_location);
    setFieldsValue({
      id_supplier: null,
      id_currency: null
    });
  };

  // handle select search supplier modal
  handleSelectModal = value => {
    const { currencies, suppliers } = this.props.supplierPriceList;
    const { setFieldsValue } = this.props.form;
    // console.log(value);
    // console.log(suppliers);
    const find_supplier = suppliers.find(
      curr_data => curr_data.supplier_code === value
    );
    if (find_supplier) {
      const curr = currencies.find(
        data => data.id_currency === find_supplier.id_currency
      );
      this.setState({ id_supplier: find_supplier.id_supplier });
      const id_currency = curr.id_currency;
      setFieldsValue(
        {
          id_currency,
          supplier_code: find_supplier.supplier_code,
          supplier_name: find_supplier.supplier_name
        },
        this.handleModalSearchSupplier()
      );
    }
    // console.log(find_supplier);
  };

  handleSubmit = e => {
    e.preventDefault();
    if (this.state.dataSource.length === 0) {
      notification("error", "Item data can not be empty");
      this.props.clearError();
    }
    this.props.form.validateFieldsAndScroll((err, value) => {
      if (!value.pricelist_code) {
        value.pricelist_code = null;
      }
      if (!err && this.state.dataSource.length > 0) {
        value.id_supplier_type
          ? (value.id_supplier_type = value.id_supplier_type)
          : (value.id_supplier_type = null);

        value.start_date = moment(this.state.start_date, "DD MMMM YYYY").format(
          "YYYY-MM-DD HH:mm:ssss"
        );
        value.expired_date = moment(
          this.state.expired_date,
          "DD MMMM YYYY"
        ).format("YYYY-MM-DD HH:mm:ssss");

        const { dataSource } = this.state;

        value.supplier_pricelist_details = [];

        dataSource.map(item => {
          value.supplier_pricelist_details.push({
            current_price: item.last_purchase_price,
            id_item: item.id_item,
            id_unit: item.id_unit,
            item_code: item.item_code,
            item_name: item.item_name,
            discount: item.discount || 0,
            item_price: item.item_price || 0
          });
        });
        if (value.supplier_pricelist_details.length === 0) {
          notification("error", "Item data can not be empty");
          this.props.clearError();
        } else {
          // console.log("submitted data", value);
          this.props.submit(value);
        }
      }
    });
  };

  handleUpdateItem = row => {
    const newData = [...this.state.dataSource];
    const index = newData.findIndex(item => row.index_no === item.index_no);
    const item = newData[index];
    newData.splice(index, 1, {
      ...item,
      ...row
    });
    this.setState({ dataSource: newData });
  };

  render() {
    // inline style
    var blockDisplay = {
      display: "block"
    };

    // form layout
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };

    const { user } = this.props.auth;
    let { permissions, default_employee_location } = user ? user : [];
    permissions = permissions ? permissions : [];
    default_employee_location = default_employee_location
      ? default_employee_location
      : [];

    // const { getSupplierPriceList } = actions;
    const { search, searchSupp } = this.state;
    const { getFieldDecorator } = this.props.form;
    const {
      currencies,
      id_location,
      items,
      loadingItems,
      loadingSubmit,
      loadingSuppliers,
      locations,
      // pricelist_info,
      suppliers,
      supplier_types
    } = this.props.supplierPriceList;

    return (
      <>
        <ModalAddSupplierType
          handleModal={this.props.handleModalAddSupplierType}
          supplierType={this.props.supplierType}
          submit={this.props.handleSubmitSupplierType}
          visible={this.props.visibleAddSupplierType}
        />
        <Form layout="vertical" onSubmit={this.handleSubmit}>
          <ModalSearchSupplier
            handleModal={this.handleModalSearchSupplier}
            handleSelectModal={this.handleSelectModal}
            idLocation={this.state.id_location}
            locations={locations}
            loading={loadingSuppliers}
            suppliersData={suppliers}
            visible={this.state.visibleModalSearchSupplier}
          />
          {/* <Row style={{ margin: "12px 0px" }}>
          <Col span={20}>
            <Steps size="small" progressDot={customDot} current={0}>
              <Step title="New" />
              <Step title="Confirmed" />
              <Step title="Rejected" />
            </Steps>
          </Col>
        </Row> */}
          {/* Form untuk supplier price list data */}
          <Divider orientation="left" style={{ fontSize: 14 }}>
            <IntlMessages id="detail" />
          </Divider>
          <div className="isoDetailDataFlex" style={{ padding: "0" }}>
            <div
              className="isoColumn-2 isoFormGroupComp"
              style={{ padding: "0 15px 0 0" }}
            >
              {/* Form item untuk pricelist code */}
              <InputGroup compact>
                <Col span={20}>
                  <Form.Item
                    label={
                      <IntlMessages id="customerPriceLists.priceListCode" />
                    }
                  >
                    {getFieldDecorator("pricelist_code", {
                      initialValue: "",
                      rules: [
                        {
                          max: 30,
                          message: <IntlMessages id="maxLength30" />
                        }
                      ]
                    })(<Input disabled={this.state.check_code} />)}
                  </Form.Item>
                </Col>
                <Col span={4}>
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="autoGenerateCode" />}
                  >
                    <Checkbox
                      style={{
                        paddingTop: "5px",
                        paddingLeft: "8px"
                      }}
                      checked={this.state.check_code}
                      onChange={this.handleChangeCode}
                    />
                  </Form.Item>
                </Col>
              </InputGroup>

              {/* Form item untuk Location */}
              <Form.Item label={<IntlMessages id="sidebar.location" />}>
                {getFieldDecorator("id_location", {
                  initialValue: default_employee_location.id_location
                    ? default_employee_location.id_location
                    : [],
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <Select
                    filterOption={true}
                    onChange={this.handleSelectLocation}
                    optionFilterProp="children"
                    placeholder={
                      <IntlMessages id="purchaseReceipts.filter.select.location" />
                    }
                    showSearch
                  >
                    {locations ? (
                      locations.map(data => (
                        <Option key={data.id_location} value={data.id_location}>
                          {data.location_code} ({data.location_name})
                        </Option>
                      ))
                    ) : (
                      <Option value="" />
                    )}
                  </Select>
                )}
              </Form.Item>

              {/* Form item untuk rangeDate */}
              <Form.Item label={<IntlMessages id="priceListRangeDate" />}>
                {getFieldDecorator("range_date", {
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <RangePicker
                    allowClear
                    onChange={this.handleChangeRangePicker}
                    format="DD MMMM YYYY"
                    style={{
                      width: "100%"
                    }}
                  />
                )}
              </Form.Item>
            </div>

            <div
              className="isoColumn-2 isoFormGroupComp"
              style={{ padding: "0 0 0 15px" }}
            >
              <Row>
                <Col span={9} style={{ paddingRight: "5px" }}>
                  {/* Form item untuk pricelist type */}
                  <Form.Item
                    label={
                      <IntlMessages id="customerPriceLists.priceListType" />
                    }
                  >
                    {getFieldDecorator("pricelist_type", {
                      initialValue: parseInt(1),
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        }
                      ]
                    })(
                      <Select
                        onChange={this.handleChangeSelectPriceListType}
                        placeholder={
                          <IntlMessages id="customerPriceLists.priceListType.select" />
                        }
                      >
                        <Option value={1}>
                          {<IntlMessages id="supplierCode" />}
                        </Option>
                        <Option value={2}>
                          {<IntlMessages id="sidebar.supplierType" />}
                        </Option>
                      </Select>
                    )}
                  </Form.Item>
                </Col>
                <Col span={15} style={{ paddingLeft: "5px" }}>
                  {this.state.priceListType === parseInt(2) ? (
                    // form untuk supplier type
                    <Form.Item
                      label={<IntlMessages id="sidebar.supplierType" />}
                    >
                      {getFieldDecorator("id_supplier_type", {
                        rules: [
                          {
                            required: true,
                            message: <IntlMessages id="required" />
                          }
                        ]
                      })(
                        <Select
                          onChange={this.handleChangeSelectSupplierType}
                          placeholder={
                            <IntlMessages id="suppliers.filter.select.supplierType" />
                          }
                        >
                          {permissions.includes("Create Supplier Type") ? (
                            <Option
                              key={"Create Supplier Type"}
                              value={undefined}
                              onClick={() =>
                                this.props.handleModalAddSupplierType()
                              }
                            >
                              <div
                                style={{
                                  color: "#1890FF",
                                  fontStyle: "italic"
                                }}
                              >
                                {" + " +
                                  getLanguage({
                                    id: "supplierTypes.pageHeader"
                                  })}
                              </div>
                            </Option>
                          ) : null}

                          {supplier_types ? (
                            supplier_types.map(data => (
                              <Option
                                key={data.id_supplier_type}
                                value={data.id_supplier_type}
                              >
                                {data.supplier_type_code} (
                                {data.supplier_type_name})
                              </Option>
                            ))
                          ) : (
                            <Option value="" />
                          )}
                        </Select>
                      )}
                    </Form.Item>
                  ) : (
                    // form untuk supplier code
                    <Form.Item label={<IntlMessages id="sidebar.supplier" />}>
                      {getFieldDecorator("id_supplier", {
                        rules: [
                          {
                            required: true,
                            message: <IntlMessages id="required" />
                          }
                        ]
                      })(
                        <Select
                          // value={searchSupp ? searchSupp : []}
                          showSearch
                          allowClear
                          // disabled={loadingSuppliers}
                          // optionFilterProp="children"
                          filterOption={false}
                          placeholder={<IntlMessages id="select.supplier" />}
                          loading={loadingSuppliers}
                          notFoundContent={
                            loadingSuppliers ? (
                              <Spin size="small" />
                            ) : (
                              <Empty
                                description={<IntlMessages id="noData" />}
                              />
                            )
                          }
                          onSelect={this.handleSelectSupplier}
                          onSearch={this.handleSearchSupplier}
                          style={{ width: "100%" }}
                        >
                          {AddModule(
                            "Create Supplier",
                            "/dashboard/setup/suppliers/add",
                            "suppliers.pageHeader",
                            this.props.history,
                            permissions
                          )}

                          {suppliers && suppliers.length > 0
                            ? suppliers.map(data => (
                                <Option
                                  key={data.id_supplier}
                                  value={data.id_supplier}
                                >
                                  {data.supplier_code} ({data.supplier_name})
                                </Option>
                              ))
                            : null}
                        </Select>
                      )}
                    </Form.Item>
                  )}
                </Col>
              </Row>

              <Row>
                <Form.Item
                  label={<IntlMessages id="supplierPriceLists.currency" />}
                >
                  {getFieldDecorator("id_currency", {
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Select
                      disabled={
                        this.state.priceListType === parseInt(1) || false
                      }
                      placeholder={<IntlMessages id="select.currency" />}
                    >
                      {currencies ? (
                        currencies.map(data => (
                          <Option
                            key={data.id_currency}
                            value={data.id_currency}
                          >
                            {data.currency_code} ({data.currency_name})
                          </Option>
                        ))
                      ) : (
                        <Option value="" />
                      )}
                    </Select>
                  )}
                </Form.Item>
              </Row>

              {/* Form item untuk description */}
              <Form.Item label={<IntlMessages id="suppliers.add.notes" />}>
                {getFieldDecorator("note", {
                  //initialValue: data.description,
                  rules: []
                })(<TextArea rows={4} />)}
              </Form.Item>
            </div>
          </div>

          {/* Form untuk Item Data */}
          <div style={blockDisplay}>
            <Divider orientation="left" style={{ fontSize: 14 }}>
              <IntlMessages id="itemDetails" />
            </Divider>
            <div className="isoDetailDataFlex" style={{ padding: "0" }}>
              <div
                className="isoColumn-1 isoFormGroupComp"
                style={{ padding: "0" }}
              >
                <Row style={{ marginBottom: "40px" }}>
                  <Col span={24}>
                    <Form.Item {...formItemLayout}>
                      <Select
                        value={search ? search : []}
                        showSearch
                        allowClear
                        // optionFilterProp="children"
                        filterOption={false}
                        placeholder={
                          <IntlMessages id="purchaseOrders.item.placeholder" />
                        }
                        loading={loadingItems}
                        notFoundContent={
                          loadingItems ? (
                            <Spin size="small" />
                          ) : id_location ? (
                            <Empty description={<IntlMessages id="noData" />} />
                          ) : (
                            <Empty
                              description={
                                <IntlMessages id="purchaseOrders.item.noLocation" />
                              }
                            />
                          )
                        }
                        onSelect={this.handleSelectItem}
                        onSearch={this.handleSearchItem}
                      >
                        {AddModule(
                          "Create Item",
                          "/dashboard/setup/items/add",
                          "items.pageHeader",
                          this.props.history,
                          permissions
                        )}

                        {items.map(data => {
                          return !this.state.selected_items.includes(
                            data.id_item + "_" + data.id_unit
                          ) ? (
                            <Option
                              key={data.id_item + "_" + data.id_unit}
                              value={data.id_item + "_" + data.id_unit}
                              style={{ width: "100%" }}
                            >
                              <Row>
                                <Col span={14}>
                                  <span>
                                    {data.item_code} ({data.item_name})
                                  </span>
                                </Col>
                                <Col span={10}>
                                  <span>
                                    {data.unit_name} (
                                    <NumberFormat
                                      value={parseFloat(data.unit_conversion)}
                                    />{" "}
                                    {" " + data.base_unit.unit_name})
                                  </span>
                                </Col>
                              </Row>
                            </Option>
                          ) : null;
                        })}
                      </Select>
                    </Form.Item>
                  </Col>
                  <Col span={24}>
                    <EditableTable
                      dataSource={this.state.dataSource}
                      handleDeleteItem={this.handleDeleteItem}
                      handleUpdateItem={this.handleUpdateItem}
                      status={1}
                    />
                  </Col>
                </Row>
              </div>
            </div>
          </div>

          {/* Submit Button */}
          <Form.Item>
            <Button
              type="primary"
              htmlType="submit"
              // style={{ marginLeft: "50px" }}
              loading={loadingSubmit}
              disabled={
                !permissions.includes("Create Supplier Pricelist")
                  ? true
                  : false
              }
            >
              <IntlMessages id="button.submit" />
            </Button>
          </Form.Item>

          {/* Form */}
        </Form>
      </>
    );
  }
}

const WrappedSupplierPriceListCreateForm = Form.create({
  name: "create_supplier_price_list"
})(SupplierPriceListCreateForm);

export default WrappedSupplierPriceListCreateForm;
