import React, { Component } from "react";
import { compose } from "redux";
import { connect } from "react-redux";
import Button from "../../../../components/uielements/button";
import SupplierPriceListCreateForm from "./createForm";
import IntlMessages from "../../../../components/utility/intlMessages";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import notification from "../../../../components/notification";
import PageHeader from "../../../../components/utility/pageHeader";
import { Box } from "../../../App/app.style";
import actions from "../../../../redux/promotion/supplierPriceList/actions";
import supplierTypeActions from "../../../../redux/supplier/supplierType/actions";

const redirecTo = "/dashboard/promotions/supplier-price-lists";
const { storeSupplierType } = supplierTypeActions;

class CreateSupplierPriceList extends Component {
  state = {
    visibleAddSupplierType: false
  };

  componentDidMount() {
    const { createSupplierPriceList } = this.props;
    createSupplierPriceList();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.supplierPriceList.error) {
      notification("error", this.props.supplierPriceList.error);
      this.props.clearError();
    }
    if (
      this.props.supplierPriceList.success ||
      this.props.supplierPriceList.warning
      // this.props.supplierPriceList.id_supplier_pricelist
    ) {
      this.props.history.replace(
        redirecTo +
          "/update/" +
          this.props.supplierPriceList.id_supplier_pricelist
      );
      // this.props.history.replace(redirecTo);
      // notification("success", this.props.purchaseOrder.success);
      // this.props.clearSuccess();
    }

    // Supplier Type
    if (this.props.supplierType.error) {
      notification("error", this.props.supplierType.error);
      this.props.clearError();
    }

    if (this.props.supplierType.success) {
      const { createSupplierPriceList, clearSuccess } = this.props;
      notification("success", this.props.supplierType.success);
      this.handleModalAddSupplierType();
      clearSuccess();
      createSupplierPriceList();
    }
  }

  handleModalAddSupplierType = () => {
    this.setState({
      visibleAddSupplierType: !this.state.visibleAddSupplierType
    });
  };

  handleSubmit = value => {
    const { storeSupplierPriceList } = this.props;
    storeSupplierPriceList(value);
    // console.log(this.props.supplierPriceList);
  };

  handleSubmitSupplierType = value => {
    const { storeSupplierType } = this.props;
    storeSupplierType(value);
  };

  render() {
    const user = this.props.auth.user;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            // marginBottom={true}
            title={<IntlMessages id="supplierPriceLists.button.add" />}
          >
            <Button onClick={() => this.props.history.replace(redirecTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <SupplierPriceListCreateForm
            auth={this.props.auth}
            getItemsForSupplierPriceList={
              this.props.getItemsForSupplierPriceList
            }
            getSuppliersBasedOnLocation={this.props.getSuppliersBasedOnLocation}
            history={this.props.history}
            permissions={permissions}
            submit={this.handleSubmit}
            supplierPriceList={this.props.supplierPriceList}
            //Supplier Type
            supplierType={this.props.supplierType}
            visibleAddSupplierType={this.state.visibleAddSupplierType}
            handleModalAddSupplierType={this.handleModalAddSupplierType}
            handleSubmitSupplierType={this.handleSubmitSupplierType}
          />
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    auth: state.auth,
    supplierPriceList: state.supplierPriceList,
    supplierType: state.supplierType
  };
};

export default compose(
  connect(mapStateToProps, { ...actions, storeSupplierType })
)(CreateSupplierPriceList);
